/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  padding: 0 2rem;
  min-height: 100vh;
  background: rgb(249, 84, 84);
  background: linear-gradient(259deg, rgba(249, 84, 84, 1) 26%, rgba(231, 92, 100, 1) 84%);
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  max-width: 25rem;
  margin: 0 auto;
  padding: 1rem;
}
.App-header h1 {
  font-family: Segoe UI light, sans-serif;
  font-size: 3rem;
  letter-spacing: 0.3rem;
  word-spacing: 0.3rem;
  margin-top: 2rem;
}
.todo__form {
}
.todo__form-text {
  height: 2.5rem;
  border-radius: 5px;
  border: none;
  font-size: 1.5rem;
  display: inline-block;
  padding: 0 10px;
  outline: none;
  width: 100%;
  max-width: 400px;
  box-sizing: border-box;
}

.todo__list {
  margin-top: 20px;
  width: 100%;
  max-width: 500px;
  background-color: white;
  box-shadow: 0 0 62px -30px;
  border-radius: 2px;
  box-sizing: border-box;
}

.todo__item {
  cursor: pointer;
  display: flex;
  line-height: 2.3rem;
  padding: 0 10px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  align-items: center;
}
.todo__item:not(:last-child):after {
  border-bottom: 1px solid #a2a2a2;
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 95%;
  transform: translateX(-50%);
}
.todo__item-check {
  width: 1.5rem;
  height: 1.5rem;
  margin-left: auto;
  border: 1px solid gray;
  border-radius: 42px;
  color: #5ebb5e;
  font-size: 22px;
  display: flex;
  align-items: center;
}
.todo__item-text {
  flex: 1;
  text-align: left;
  text-transform: capitalize;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 24px;
  color: #4e4941;
}
.todo__item:hover {
  background-color: whitesmoke;
}
.todo__item:not(:last-child) {
}
.btn__clear {
  height: 2rem;
  width: 2rem;
  margin: 5px auto;
  display: block;
  border-radius: 2rem;
  border-style: none;
  border: 2px solid #f45658;
  color: #de383d;
  cursor: pointer;
  background: #f1f1f1e8;
  box-shadow: 0 0 25px -11px black;
  font-size: 16px;
  
}
.diplay{
  display: none;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
